  getReservations(args: ChangeReservationArg): Observable<object> {
    const res = new ReservationClass();

    let list1 = this.loadRooms(args);

    res.rooms  = list1;

    let list2 = this.loadBookings(args);

    res.bookings = list2;

    return of(res);
  }

  getReservationByName(args: SearchReservationArg): Observable<object> {
    const persons = new Array<PersonClass>();

    if (args.year === 0 && args.month === 0 && args.name === '') {
      return of(persons);
    }

    let list = this.loadBookings(args);
    if (args.year !== 0) {
      list = list.filter(l => l.date_from.getFullYear() === args.year);
    }
    if (args.month !== 0) {
      list = list.filter(l => l.date_from.getMonth() === args.month - 1);
    }
    if (args.name !== '') {
      list = list.filter(l => l.booking_ref.startsWith(args.name) === true);
    }

    for (const b of list) {
      const p = new PersonClass();
      p.id = b.id;
      p.rental_unit_id = b.rental_unit_id;
      p.capacity = b.capacity;
      p.name = this.getRoomById(p.rental_unit_id).name;
      
      p.date_from = b.date_from;
      p.date_to = b.date_to;
      p.nb_nights = b.nb_nights;
      p.name = b.booking_ref;
      persons.push(p);
    }

    return of(persons);
  }


  private formatGMY(date: Date): string {
    return date.getDate() + '-' + (date.getMonth() + 1) + '-' + date.getFullYear();
  }

  private maxValue(list: ConsumptionClass[]): number {
    return list.reduce((max, p) => p.id > max ? p.id : max, list[0].id);
  }


  private getRoomById(id: number): RentalUnitClass {
    const list = this.loadRooms({id: id});
    const r = list.filter(l => l.id === id)[0];
    return r;
  }

